






















import Swal from 'sweetalert2';
  import { Component, Vue } from 'vue-property-decorator';
  import { State, Action } from 'vuex-class';
  import { IListKeyValItem, ILoginPayload, ITags } from '@/types/interface';
  import Banner from '../../components/Banner.vue';
  import BaseSearchSelection from '../../components/BaseSearchSelection.vue';
  import BaseFooter from '../../components/BaseFooter.vue';
  import BaseLabelList from '../../components/BaseLabelList.vue';

  @Component({
    components: {
      Banner,
      BaseSearchSelection,
      BaseFooter,
      BaseLabelList,
    },
  })

  export default class InterestExam extends Vue {
    @Action('getTag', { namespace: 'tags' }) getTag!: () => Promise<void>;
    @Action('getAllTag', { namespace: 'tags' }) getAllTag!: () => void;
    @Action('addTags', { namespace: 'tags' }) addTags!: (list: string[] | []) => Promise<ILoginPayload>;
    @State('tags', { namespace: 'tags' }) tags!: ITags[] | [];
    @State('selectedTags', { namespace: 'tags' }) selectedTags!: ITags[];

    listOne: string[] = [];
    listTwo: string[] = [];

    get tempList(): IListKeyValItem[] | [] {
      return [...this.tags].map((d: ITags) => ({
        key: d.value,
        val: d.text,
      }));
    }

    get typeList() {
      return this.tempList.filter((d: IListKeyValItem) => d.key.indexOf('AA') >= 0);
    }

    get kindList() {
      return this.tempList.filter((d: IListKeyValItem) => d.key.indexOf('AB') >= 0);
    }

    get isMemberProcess() {
      return this.$route.query.process === 'member';
    }

    created() {
      this.getAllTag();
      this.getTag()
        .then(() => {
          if (this.selectedTags.length !== 0) {
            this.listOne = this.selectedTags.filter((d: ITags) => d.value.indexOf('AA') >= 0).map((d: ITags) => d.text);
            this.listTwo = this.selectedTags.filter((d: ITags) => d.value.indexOf('AB') >= 0).map((d: ITags) => d.text);
          }
        });
    }

    link() {
      this.$router.push('/member');
    }

    skip() {
      this.$router.push('/member/edit_subscribe');
    }

    save() {
      const listOneTemp = this.listOne.map((d: string) => {
        const temp = this.typeList.find((item: IListKeyValItem) => item.val === d);
        return temp ? temp.key : '';
      });

      const listTwoTemp = this.listTwo.map((d: string) => {
        const temp = this.kindList.find((item: IListKeyValItem) => item.val === d);
        return temp ? temp.key : '';
      });

      this.addTags([...listOneTemp, ...listTwoTemp])
        .then((data: ILoginPayload) => {
          if (data.Code === '0000') {
            if (this.isMemberProcess) {
              Swal.fire({
                toast: true,
                width: 300,
                position: 'top',
                showConfirmButton: false,
                showCloseButton: true,
                background: '#EDFAF5',
                buttonsStyling: false,
                customClass: {
                  closeButton: 'success-toast-close-btn',
                  popup: 'success-toast-popup',
                  title: 'success-toast-title',
                },
                title: '儲存成功',
              });
            } else {
              this.$router.push('/member/edit_subscribe');
            }
          }
        });
    }
  }
