















import clickOutside from 'vue-click-outside';
import {
  Component, Model, Prop, Vue,
} from 'vue-property-decorator';
import { IListKeyValItem } from '@/types/interface';

@Component({
  directives: {
    clickOutside,
  },
})

export default class BaseSearchSelection extends Vue {
  @Prop({ default: () => [] }) list!: IListKeyValItem[] | [];
  @Model('change', { default: () => [] }) readonly checked!: [string];

  filterTxt = '';
  isShow = false;

  get filterList() {
    if (this.filterTxt === '') return this.list;
    return this.list.filter((item: IListKeyValItem) => item.val.indexOf(this.filterTxt) >= 0);
  }

  get checkedList() {
    return this.checked;
  }

  set checkedList(list) {
    this.$emit('change', list);
  }

  onClickAway() {
    console.log('onClickAway');
  }

  hide() {
    this.isShow = false;
  }
}
